.main-bg{
  background-color: hsl(0, 100%, 74%);
  position: relative;
  height: 635px;
}
#mobileimg{
  display: none;
}
img{
  width: 100%;
  height: 100%;
}

.main-content{
  position: absolute;
  top:0;
  width: 100%;
  height: 100%;
}

.input-flex{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 7%;
  height: 100%;
}

.input-text,
.input-box{
  width:50%;
}

.input-text{
  color: white;
  padding: 0 20px 0 50px;
  text-align: justify;
}
.input-text div{
  font-weight: 600;
  margin-top: 30px;
}
.input-text h2{
  font-size: 50px;
  font-weight: 700;
  width: 80%;
}

.input-box{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.try-out{
  background-color: midnightblue;
  color: white;
  width: 80%;
  padding: 10px 15px;
  border-radius: 5px;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
}

.try-out span{
  color: hsl(246, 25%, 77%);
}

form{
  /* display: flex; */
  /* flex-direction: column; */
  background-color: white;
  padding:3% 5%;
  width: 80%;
  border-radius: 10px;
  margin-top: 10px;
}

.input{
  margin-top: 15px;
  position: relative;
}
input{
  border:none;
  outline: none;
  border: 1px solid hsl(246, 25%, 77%);
  padding: 13px 30px;
  transition: .5s;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 600;
  width: 100%;
 
}

input:focus {
  border: 1px solid black;
}

input::placeholder{
  /* color: grey; */
  font-size: 16px;
  font-weight: 600;
}
.err-msg{
  color: hsl(0, 100%, 74%);
  font-size: 13px;
  text-align: right;
  font-weight: 600;
  display: none;
  margin-top: 5px;
  padding-right: 5px;
}
.errorIcon{
  color: white;
  background-color: hsl(0, 100%, 74%);
  width: 25px;
  height: 25px;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: absolute;
  top:14.5px;
  right: 7%;
  display: none;
}

.claim{
  background-color: hsl(154, 59%, 51%);
  padding: 15px;
  text-align: center;
  border-radius: 5px;
  text-transform: uppercase;
  color: white;
  font-weight: 700;
  font-size: 16px;
  margin-top: 25px;
}

.claim:hover{
  cursor: pointer;
  box-shadow: 0 7px 5px hsl(154, 58%, 45%);
}

form p{
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  margin-top: 5px;
  color: hsl(246, 25%, 77%);
}

form p a{
  color: hsl(0, 100%, 74%);
  text-decoration: none;
}

@media screen and (max-width: 375px) {
  #desktopimg{
    display: none;
  }
  #mobileimg{
    display: block;
  }
  .main-bg{
    background-color: hsl(0, 100%, 74%);
    position: relative;
    height: 1000px;
  }
  .input-flex{
    display: block;
    height: 100%;
  }

  .input-text,
  .input-box{
    width:100%;
  }

  .input-text{
    padding: 0 ;
    margin-bottom: 10%;
    padding-top: 20%;
  }
  .input-text h2{
    font-size: 40px;
    font-weight: 700;
    width: 100%;
  }

  .try-out{
    width: 100%;
    padding: 5%;
    font-weight: 600;
  }

  form{
    padding: 5% 6%;
    width: 100%;
    margin-top: 8%;
  }
  
  .input{
    margin-top: 18px;
  }

  input{
    padding: 10px ;
  }
  .errorIcon{
    top:12.5px;
  }
  form p{
    text-align: center;
    font-size: 12px;
    font-weight: 700;
    margin: 2% 5%;
    color: hsl(246, 25%, 77%);
  }
}
@media (min-width: 377px) and (max-width: 900px){
  #mobileimg{
    display: block;
  }
  .main-bg{
    height: 1000px;
  }
  .input-flex{
    display: block;
    height: 100%;
    margin: 0 15%;
  }

  .input-text,
  .input-box{
    width:100%;
  }

  .input-text{
    padding: 0 ;
    margin-bottom: 10%;
    padding-top: 20%;
  }
  .input-text h2{
    font-size: 40px;
    font-weight: 700;
    width: 100%;
  }

  .try-out{
    width: 100%;
    padding: 5%;
    font-weight: 600;
  }

  form{
    padding: 5% 6%;
    width: 100%;
    margin-top: 8%;
  }
  
  .input{
    margin-top: 18px;
  }

  input{
    padding: 10px ;
  }
  .errorIcon{
    top:12px;
  }
  form p{
    text-align: center;
    font-size: 12px;
    font-weight: 700;
    margin: 2% 5%;
    color: hsl(246, 25%, 77%);
  }
}